import { useEffect, useState } from "react";
import axios from "axios";
import { useRouter } from "next/router";

export default function ChatHistory() {
  const [chats, setChats] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [editingChat, setEditingChat] = useState(null);
  const [title, setTitle] = useState("");
  const router = useRouter(); // Tambahkan router untuk navigasi

  useEffect(() => {
    fetchChats();
  }, []);

  const fetchChats = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.get("http://localhost:3000/api/history", {
        headers: { Authorization: `Bearer ${token}` },
      });
      setChats(response.data);
    } catch (error) {
      console.error("Error fetching chats:", error.response?.data || error.message);
    }
  };

  const handleCreateOrUpdate = async () => {
    try {
      const token = localStorage.getItem("token");
      if (!token) throw new Error("Token tidak ditemukan");

      if (!title.trim()) {
        alert("Title tidak boleh kosong");
        return;
      }

      if (editingChat) {
        // Update chat
        await axios.put(
          `http://localhost:3000/api/history?id=${editingChat._id}`,
          { title },
          { headers: { Authorization: `Bearer ${token}` } }
        );
      } else {
        // Create chat
        await axios.post(
          "http://localhost:3000/api/history",
          { title },
          { headers: { Authorization: `Bearer ${token}` } }
        );
      }

      fetchChats();
      closeModal();
    } catch (error) {
      console.error("Error saving chat:", error.response?.data || error.message);
    }
  };

  const handleDelete = async (id) => {
    try {
      const token = localStorage.getItem("token");
      await axios.delete(`http://localhost:3000/api/history?id=${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      fetchChats();
    } catch (error) {
      console.error("Error deleting chat:", error.response?.data || error.message);
    }
  };

  const openModal = (chat = null) => {
    setEditingChat(chat);
    setTitle(chat ? chat.title : "");
    setModalOpen(true);
  };

  const closeModal = () => {
    setEditingChat(null);
    setTitle("");
    setModalOpen(false);
  };

  return (
    <div className="bg-black pb-[1080px] p-3">
      <h1>Chat History</h1>
      <button onClick={() => openModal()}>+ Tambah Chat</button>
      <ul>
        {chats.map((chat) => (
          <li className="text-white/75 pt-3 pb-3 border-b border-white/15" key={chat._id}>
            <p className="text-xl pb-2">{chat.title}{" "}</p>
            <button className="text-xs px-5 py-2 border border-white/15 rounded-full" onClick={() => openModal(chat)}>Edit</button>
            <button className="text-xs px-5 py-2 border border-white/15 rounded-full" onClick={() => handleDelete(chat._id)}>Hapus</button>
            <button className="text-xs px-5 py-2 border border-white/15 rounded-full" onClick={() => router.push(`/history/${chat._id}`)}>Lihat Detail</button>
          </li>
        ))}
      </ul>

      {/* Modal */}
      {modalOpen && (
        <div className="fixed top-0 left-0 right-0 flex flex-col justify-center h-screen bg-black text-white">
          <h2 className="ml-5">{editingChat ? "Edit Chat" : "Tambah Chat"}</h2>
          <input
            className="bg-transparent border border-white/15 p-3 m-3 rounded-xl"
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="Masukkan judul chat"
          />
          <div className="flex justify-between items-center ml-5 mr-5">
            <button onClick={handleCreateOrUpdate}>
              {editingChat ? "Simpan Perubahan" : "Tambah"}
            </button>
            <button onClick={closeModal}>Batal</button>
          </div>
        </div>
      )}
    </div>
  );
}
